/* 기본 테이블 스타일 */
table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  max-width: 100%;
  overflow-x: auto; /* 테이블이 화면 밖으로 나가면 스크롤 가능 */
}

th, td {
  border: 1px solid black;
  text-align: center;
  padding: 8px;
  min-width: 150px;
  word-wrap: break-word;
}

th {
  background-color: #f2f2f2;
}

/* 반응형 디자인 추가 */
@media screen and (max-width: 768px) {
  table {
    display: block;
    overflow-x: auto; /* 가로 스크롤 추가 */
    white-space: nowrap; /* 텍스트 줄바꿈 방지 */
  }

  th, td {
    padding: 5px;
    font-size: 12px; /* 모바일 화면에 맞게 글자 크기 조정 */
    min-width: 100px; /* 최소 너비 조정 */
  }
}